
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import srcFloor1 from '../../assets/images/floor3.gif';
import srcFloor2 from '../../assets/images/floor.png';
import srclock from '../../assets/images/lock2.png';
export default {
name: 'mapSelects',
directives: {
TransferDom
},
components: {
Flexbox,
Loading,
XDialog,
FlexboxItem
},
data () {
return {
srclock: srclock,
picImg: true,
pic: 0,
srcFloor: [srcFloor1, srcFloor2],
floors: ['1st', '2nd', '3rd', '4th', '5th', '6th', '7th'],
currentPage: '3rd',
show2: false,
selectFloor: true,
showDialog: false
};
},
methods: {
lockFloor () {
this.showDialog = true;
},
colseDialog () {
this.showDialog = false;
},

changePage (val) {
let _this = this;
this.show2 = true;
this.selectFloor = false;
this.pic = 1;
setTimeout(() => {
_this.show2 = false;
_this.currentPage = val;
}, 1000);
}
}
};



