
import store from '@/store/index';
import { mapActions } from 'vuex';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Tab1 from './components/tabMap.vue';
import Tab2 from './components/tabEvent.vue';
import Tab3 from './components/tabInformation.vue';
import LogoTop from './components/LogoTop.vue';
export default {
name: 'HelloWorld',
data () {
return {
list2: ['Map', 'Events', 'Information'],
tabView: 'Tab1',
indextab: 0,
currentIndex: 0,
demo2: 'Buildings of Schools'
};
},

components: {
Tab,
TabItem,
Tab1,
Tab2,
Tab3,
LogoTop
},
mounted () {
this.picImg = true;
if (this.$route.params.selectFloor) {
this.selectFloor = this.$route.params.selectFloor;
this.pic = 1;
this.currentPage = this.$route.params.page;
} else {
this.currentPage = 1;
}
},
methods: {

...mapActions(['loadPageConfig']),

getItem (index) {
this.indextab = index;
this.tabView = 'Tab' + (index + 1);
this.$emit('getIndex', index);
}
},
store
};



