
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import src1 from '../../assets/images/banner_1.png';
import src2 from '../../assets/images/banner_2.png';
import src3 from '../../assets/images/banner_3.png';
export default {
name: 'event',
data () {
return {
selectFloor: false,
currentIndex: 0,
indexEvent: 1,
src1: src1,
src2: src2,
src3: src3
};
},
components: {
Swiper,
SwiperItem,
Scroller
},
methods: {

preImg () {
this.selectFloor = false;
if (this.currentIndex === 0) {
this.currentIndex = 2;
} else {
this.currentIndex--;
}
},

nextImg () {
if (this.currentIndex === 2) {
this.currentIndex = 0;
} else {
this.currentIndex++;
}
}
}
};



