
import store from '@/store/index';
import { mapActions } from 'vuex';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Tab0 from './components/tabMapList.vue';
import Tab1 from './components/tabEvent.vue';
import Tab2 from './components/tabInformation.vue';
import LogoTop from './components/LogoTop.vue';
export default {
name: 'universityMapList',
data () {
return {
list2: ['Map', 'Events', 'Information'],
index2: 0,
tabView: 'tab0',
currentIndex: 0
};
},

components: {
Tab,
TabItem,
LogoTop,
Tab0,
Tab1,
Tab2
},
methods: {

...mapActions(['loadPageConfig']),

getItem (index) {
this.indextab = index;
this.tabView = 'Tab' + index;
this.$emit('getIndex', index);
}
},
store
};



